import React, { useState, useEffect } from "react";
import moment from "moment";
import Grid from "@material-ui/core/Grid";
import Checkbox from "@material-ui/core/Checkbox";

export default function MonthComponent(props) {
  const [month, setMonth] = useState([
    { value: 1, title: "Tháng 1" },
    { value: 2, title: "Tháng 2" },
    { value: 3, title: "Tháng 3" },
    { value: 4, title: "Tháng 4" },
    { value: 5, title: "Tháng 5" },
    { value: 6, title: "Tháng 6" },
    { value: 7, title: "Tháng 7" },
    { value: 8, title: "Tháng 8" },
    { value: 9, title: "Tháng 9" },
    { value: 10, title: "Tháng 10" },
    { value: 11, title: "Tháng 11" },
    { value: 12, title: "Tháng 12" },
  ]);

  const listsMonth = month.map((e, index) => {
    let checked = false;
    let id;
    for (let item of props.schedule?.listsMonth) {
      console.log(item);
      if (e.value === item.month) {
        checked = true;
        id = item._id;
      }
    }
    return (
      <Grid item xs={6}>
        <Grid container spacing={0}>
          <Grid item xs={9} style={{ padding: "10px" }}>
            <span>{e.title}</span>
          </Grid>
          <Grid item xs={3}>
            {checked ? (
              <Checkbox
                name="checkedB"
                style={{ color: "green" }}
                key={index}
                checked={checked}
              />
            ) : (
              <Checkbox
                name="checkedB"
                style={{ color: "green" }}
                key={index}
                checked={checked}
              />
            )}
          </Grid>
        </Grid>
      </Grid>
    );
  });
  return (
    <div>
      <Grid container spacing={3}>
        {listsMonth}
      </Grid>
    </div>
  );
}
